.items {
  border: 1px solid black;
  margin: 20px;
  justify-content: center;
  line-height: 30px;
  background-color: orange;
  border: orange;
  transition: 1s;
  height: 30px;
  border-radius: 10px;
  color: white;
}

.items:hover {
  transform: scale(1.04);
  box-shadow: 0px 0px 10px 5px black;
}
.charge {
  float: left;
  padding-left: 15px;
}
.btn-icon {
  float: right;
  padding-right: 15px;
  justify-content: space-evenly;
}
.amount {
  background-color: blue;
  color: aliceblue;
}

.clear_btn {
  margin: 20px auto;
  height: 50px;
}
.clear_btn .btn-ico {
  line-height: 50px;
}
