.our_from label {
  font-size: 20px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  float: left;
}
.our_from {
  display: flex;
  width: 100%;
}
.form-group {
  width: 100%;
  padding: 5px;
}

.ourform input {
  width: 100%;
  float: left;
}

.form-control {
  border: none;
}

.click_btn {
  height: 40px;
  line-height: 40px;
}

@media screen and (max-width:800px){
  .click_btn {
  line-height: 30px;
}

}
