.App {
  text-align: center;
  background-color: gray;

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
h1 {
  color: white;
  background-color: orange;
}
.main-content {
  background: whitesmoke;
  border-radius: 10px;
  max-width: 800px;
  margin: 20px auto;
  padding: 10px;
  margin: 100px auto;
}
.totalamt {
  color: black;
}
.footer{
  height: auto;
  bottom: 0;
  color:white;
}

@media screen and (max-width: 800px) {
  .main-content {
    background: white;
    max-width: 500px;
    margin: 100px auto;
    padding: 10px;
  }
}
